<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="搜索">
              <el-input v-model="searchs.keyword_search_value" placeholder="姓名/手机号"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
            <router-link to="/notice/create">
              <el-button style="margin-left: 8px;" @click="showDialog" type="success">发送所有用户</el-button>
            </router-link>
            <router-link to="/notice/create/user">
              <el-button style="margin-left: 8px;" type="success">发送指定用户</el-button>
            </router-link>
            <router-link to="/notice/create/vip">
              <el-button style="margin-left: 8px;" type="success">发送到指定VIP</el-button>
            </router-link>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="标题" align="center" />
      <el-table-column prop="type_name" label="用户类型" align="center" />
      <el-table-column prop="user_info" label="用户信息" align="center" />
      <el-table-column prop="read_user_num" label="已读人数" align="center" />
      <el-table-column prop="uptime_date" label="添加时间" align="center" />
      <el-table-column prop="address" align="center" label="操作" width="200">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="eidt(scope.row.id)">编辑</el-button>
          <el-button type="danger" size="small" @click="remove(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'
import Fecth4 from '../../utils/fetch4'

export default {
  data() {
    return {
      loading: false,
      searchs: {
        keyword_search_value: '',
      },
      tableData: [],
      count: 0,
      page_curren: 1,
      config: {
        type: null,
        status: null,
      },
      id: 0,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    showDialog() {
      this.is_show_dialog = true
    },
    remove(id) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true

            this.loading = true
            Fecth4('/user/notice/delete/' + id)
              .then(() => {
                done()
                this.loading = false
                instance.confirmButtonLoading = false
                this.FetchList()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                this.loading = false
              })
          } else {
            done()
          }
        },
      })
    },
    eidt(id) {
      this.$router.push('/notice/edit/' + id)
    },

    FetchList() {
      this.loading = true
      Fecth('/user/notice/getlist', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
  },
}
</script>
